.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
  
    & + & {
      margin-left: 4px;
    }

    @for $index from 1 through 5 {
      .stars--#{$index} &:nth-child(-n + #{$index}) {
        background-image: url(../images/star-active.svg);
      }
    }
  }
}
